  .s-box p {
    color: rgba(87, 105, 117, 0.9);
    text-align: center;
  }
  .s-bar {
    width: 100px;
    height: 6px;
    position: absolute;
    left: 50%;
    top: 0px;
    transform: translateX(-50%);
    background-color: #8c30f5;
    border-radius: 0px 0px 10px 10px;
    display: none;
    animation: s-bar 0.5s;
  }
  .s-box:hover {
    box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.08);
    transition: all ease 0.3s;
  }

 .s-box img {
    height: 75px;
    border-radius: 10px;
  }
  @keyframes bar {
    0% {
      width: 0px;
    }
    100% {
      width: 100px;
    }
  }
